import { Sp_tipe } from 'src/app/sdk/hr/models/sp-_tipe';
import { PesanService } from './../../../../../../services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { DirektoriSpTipeService } from 'src/app/sdk/hr/services/direktori-sp-tipe.service';
import { Component, OnInit } from '@angular/core';
import { FormGroupTypeSafe, FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { getMenuData } from 'src/app/services/menu.service.config';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private direktoriSpTipeService: DirektoriSpTipeService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private router: Router,
        private pesan: PesanService,
        private activatedRoute: ActivatedRoute
    ) { }

    data: Sp_tipe

    ngOnInit() {
        this.id = this.activatedRoute.snapshot.paramMap.get('id_sp_tipe');
        this.form();
        this.getData()
    }

    id: string
    getData() {
        this.direktoriSpTipeService.getDirektoriSpTipesId(this.id).subscribe(
            data => {
                this.dataForm.setValue(data.data)
            }
        )
    }

    dataForm: FormGroupTypeSafe<Sp_tipe>;
    form() {
        this.dataForm = this.fb.group<Sp_tipe>(
            {
                id_sp_tipe: new FormControl('', [Validators.required]),
                sp: new FormControl(''),
                keterangan: new FormControl(''),
                created_at: new FormControl(''),
                Updated_at: new FormControl(''),

            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.data = this.dataForm.value;
        this.direktoriSpTipeService.putDirektoriSpTipesId({
            id: this.id,
            body: this.data
        }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/direktori/pengaturan/sp/list'])
    }
}
