import { Sp_tipe } from 'src/app/sdk/hr/models/sp-_tipe';
import { DirektoriSpTipeService } from 'src/app/sdk/hr/services/direktori-sp-tipe.service';
import { Component, OnInit } from '@angular/core';
import { FilterFuctionService } from 'src/app/services/filter-fuction.service';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private direktoriSpTipeService: DirektoriSpTipeService,
        private filterFuctionService: FilterFuctionService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService
    ) { }

    ngOnInit() {
        this.getData()
    }

    listOfDisplayData: Sp_tipe[] = []
    listOfData: Sp_tipe[] = []
    sortValue: string | null = null
    sortKey: string | null = null
    search: string = ''
    getData() {
        this.spinner.show()
        this.direktoriSpTipeService.getDirektoriSpTipes(JSON.stringify({ sortKey: this.sortKey, sortValue: this.sortValue, search: this.search })).subscribe(
            data => {
                this.listOfDisplayData = data.data;
                this.listOfData = data.data;
                this.spinner.hide()
            }
        )
    }

    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key
        this.sortValue = sort.value
        this.getData()
    }



    updateFilter(value: string): void {
        this.search = value
        this.getData()
    }

    delete(id) {
        this.spinner.show();
        this.direktoriSpTipeService.deleteDirektoriSpTipesId(id).subscribe(
            data => {
                this.getData();
                this.spinner.hide();
            }
        )
    }

    batalDelete() {
        this.nzMessageService.info('click cancel');
    }

}
